.investor-parent-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 6rem;
    padding: 6em;
    padding-top: 8rem;
    /* margin-right: 7%; */
    /* margin-top: 10%; */
}

.value{
        
font-family: 'Inter';
font-style: normal;
font-weight: 600;
font-size: 18px;
line-height: 22px;
text-transform: uppercase;

color: #4A90E2;

}

.investor-left-content{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 1em;
    margin-left: 4%;
}

.invest-primary-heading{

font-family: 'DM Sans';
font-style: normal;
font-weight: 700;
font-size: 58px;
line-height: 65px;
align-items: center;
text-transform: capitalize;
color: #000033;
}

.invest-primary-text{

    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    display: flex;
    align-items: center;
    width: 73%;
    color: #000033;
}

.invest-right-content img{
    /* width: 100%; */
    /* height: auto; */
    width: 1100px;
    height: 600px;
    /* margin-left: 20%; */
}

.ibtn{
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    width: 130%;
    padding: 1em;
    color: black;
    /* background: #4A90E2; */
    border: 2px solid #4A90E2;
    border-radius: 20px;
}

.ibtn:hover{
    background: #4A90E2;
    color: white;
}

/* Responsive styles */
@media (max-width: 768px) {
    .investor-parent-container {
        flex-direction: column;
        padding: 2em;
        
    }

    .investor-left-content {
        width: 100%;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .invest-right-content{
        width: 100%;
        align-items: center;
        justify-content: center;
        text-align: center;   
    }

    .invest-primary-heading {
        font-size: 36px;
        line-height: 45px;
        text-align: center;
    }

    .invest-primary-text {
        width: 100%;
        text-align: center;
    }

    .ibtn {
        width: 100%;
    }
}

@media (max-width: 480px) {
    .value {
        font-size: 16px;
        line-height: 20px;
    }

    .investor-parent-container {

        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .invest-primary-heading {
        font-size: 30px;
        line-height: 40px;
    }

    .invest-primary-text {
        font-size: 14px;
        line-height: 20px;
    }

    .ibtn {
        padding: 0.8em;
    }
}